<template>
  <v-card>
    <v-card-title
      class="headline font-weight-regular blue-grey darken-2 white--text"
      >Edit Your Profile<v-spacer></v-spacer>
      <v-btn text dark><v-icon>mdi-box</v-icon></v-btn>
    </v-card-title>
    <v-form v-model="valid">
      <v-container fluid>
        <v-row wrap flat justify="center">
          <v-col cols="10" xs="10" md="7">
            <v-card height="200" dark>card</v-card>
            <v-text-field
              v-model="firstname"
              :rules="nameRules"
              :counter="30"
              label="Address"
              required
            ></v-text-field>
          </v-col>

          <v-col cols="10" xs="10" md="5">
            <v-text-field
              v-model="firstname"
              :rules="nameRules"
              :counter="10"
              label="First Name"
              required
            ></v-text-field>
            <v-text-field
              v-model="lastname"
              :rules="nameRules"
              :counter="10"
              label="Last name"
              required
            ></v-text-field>
            <v-text-field
              v-model="email"
              :rules="emailRules"
              label="E-mail"
              required
            ></v-text-field>
            <v-btn large class="mt-3 float-right blue-grey darken-2 white--text"
              >Submit</v-btn
            >
          </v-col>
        </v-row>
      </v-container>
    </v-form>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    valid: false,
    firstname: "",
    lastname: "",
    nameRules: [
      v => !!v || "Name is required",
      v => v.length <= 10 || "Name must be less than 10 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+/.test(v) || "E-mail must be valid"
    ]
  })
};
</script>
